/**editer les informations d'une machine */
import React, { Component } from 'react';
import axios from 'axios';
import { url_managers, url_roles } from '../../../urls';
import ContentWrapper from '../Layout/ContentWrapper';
import FormValidator from '../../Common/FormValidator';
import swal from 'sweetalert';
import HeadingMenu from '../HeadingMenu';
import Manager from './AppUpManager';
import { createManager } from '../../Authentication/AuthFunction';

const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};

class AddManager extends Component {
    constructor() {
        super();        
        /*Ajouter les fonctionnalités par la suite, les horaires ou le planning */
        this.state = {
            formManager : {
                nom: '',
                email: '',
                role: '',
            },
            roles : [],
            selectedRole : {},
            isLoading: true,
            activeStep: '1',
            progressValue: [100, 0, 0],
            path: [
                { 
                    label: 'Home',
                    value: '/dashboard_admin',
                },
                { 
                    label: 'Ajouter un manager',
                    value: '',
                }
            ]
        };
    }

    componentDidMount() {
        this.getRoles();
    }

    getRoles = () => {
        axios.get(url_roles)
        .then(res => {
            const roles = res.data.data
            console.log(roles)
            this.setState({
                formManager : {
                    role: roles[0]
                },
                roles: roles,
                isLoading: false
            })
            console.log(this.state.roles);
        })
        .catch((error) => {
            console.log("get roles failed!", error)
            swal("Desolée!", "Une erreur est survenue, contacter notre service client au numéro +237-655-244-570", "warning")
        })
    }

    toggleStep = activeStep => () => {
        // For submit we can obtain the form from the event
        // but for each step we need a global ref to the element
        const form = this.formManagerRef;
        // To validate only the inputs in the current steps, we use an id to query the tabPane
        // and then find all form elements for the current step only.
        const tabPane = document.getElementById('tabPane' + this.state.activeStep);
        const inputs = [].slice.call(tabPane.querySelectorAll('input,select'));
        // Run validation of inputs
        const { errors, hasError } = FormValidator.bulkValidate(inputs);

        // Update state so the validation message are shown/hidden
        this.setState({
            [form.name]: {
                ...this.state[form.name],
                errors
            }
        });

        // and prevent change the if form is not valid
        if (!hasError) {
            const progessValue1 = [100, 0, 0];
            const progessValue2 = [0, 100, 0];
            const progessValue3 = [0, 0, 100];
            
            if (this.state.activeStep !== activeStep) {
                this.setState({
                    activeStep
                });
            }
            
            switch (activeStep) {
                case '1':
                    this.setState({
                        progressValue: progessValue1
                    });
                    break;
                case '2':
                    this.setState({
                        progressValue: progessValue2
                    });
                    break;
                case '3':
                    this.setState({
                        progressValue: progessValue3
                    });
                    break;
                default:
                    console.log('default')
            }
        }
    }

    /**
      * Validate input using onChange event
      * @param  {String} formName The name of the form in the state object
      * @return {Function} a function used for the event
    */
    validateOnChange = event => {
        const input = event.target;
        const form = input.form;
        const value = input.type === 'checkbox' ? input.checked : input.value;

        const result = FormValidator.validate(input);

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                [input.name]: value,
                errors: {
                    ...this.state[form.name].errors,
                    [input.name]: result
                }
            }
        });

        if (input.name == 'role') {
            // update les informations sur le rôle
            const selectedRole = this.state.roles.find(role => role.nom == value)
            console.log(selectedRole )       
            this.setState({
                selectedRole  : selectedRole,
            })
        }
    };

    /* Simplify error check */
    hasError = (formName, inputName, method) => {
        return  this.state[formName] &&
                this.state[formName].errors &&
                this.state[formName].errors[inputName] &&
                this.state[formName].errors[inputName][method]
    }    

    handleSubmit = e => {
        const form = e.target;
        const inputs = [...form.elements].filter(i => ['INPUT', 'SELECT'].includes(i.nodeName))

        const { errors, hasError } = FormValidator.bulkValidate(inputs)

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                errors
            }
        });

        console.log(hasError ? 'Form has errors. Check!' : 'Form Submitted!')
        e.preventDefault();
        console.log(hasError)
        if (!hasError) {
            this.addManager();
        }     
    }
     
    addManager = () => {
        const { nom, email } = this.state.formManager;
        axios.post(url_managers, {
            nom,
            email,
            role: this.state.selectedRole,
        })
        .then((docRef) => {
            this.setState({
                formManager : {            
                nom: '',
                email: '',
                role: '',
                },
                selectedRole : {},
            });
            //feedback
            console.log("Un manager de plus !");
            swal("Super!", `Manager ${nom} ajouté avec succès`, "success").then((value) => {
                this.props.history.push("/list_manager/")
            }) 
        })
        .catch((error) => {
          console.error("Error adding manager: ", error);
          swal("Desolée!", error.message , "warning")
        });
    }

    // Keep a reference to the form to access from the steps methods
    formRef = node => (this.formManagerRef = node);    

    render() {
        console.log(this.state.formManager)
       
        return (            
            <ContentWrapper>
                <HeadingMenu path = {this.state.path}/>
                <Manager 
                manager={this.state}
                formRef={this.formRef}
                toggleStep={this.toggleStep}
                validateOnChange = {this.validateOnChange}
                hasError={this.hasError}
                handleSubmit = {this.handleSubmit} />
            </ContentWrapper>
        
        );
    }

}

export default AddManager;
