import firebase from '../../firebase';
import axios from 'axios';
import {url_managers} from '../../urls';


// L'administrateur ajoute les managers
const createManager = async (email, name) => {
    console.log(email)
    // generated password
    const password = (Math.random() + 1).toString(36).substring(5)
    console.log(password)
    try {
        const userCredential = await firebase.auth().createUserWithEmailAndPassword(email, password)
        console.log(`User ${email} added succefully!`)
        const user = userCredential.user
        const response = {
            password,
            user,
        }
        return (response) 
    } 
    catch (error) {
        throw new Error(error.message)
    }
};

// Login
const signIn = async (email, password) => {
    console.log(email, password)
    const userCredential = await firebase.auth().signInWithEmailAndPassword(email, password)
    console.log("user : ", userCredential)
    console.log(`Sign in succeed ${email}!`);
    return (userCredential) 
}

const signOut = async () => {
    firebase.auth().signOut()
}

export { 
    signIn, 
    createManager,
    signOut,
}