/** modifier/ajouter les informations d'un manager */
import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import {
    Card,
    CardBody,
    CardHeader,
    Row,
    Progress,
    Col,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    Form,
    NavItem,
    NavLink, } 
from 'reactstrap';
import classnames from 'classnames';
const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};

/*color for next and previous button */
const previousColor = "inverse"
const nextColor = "success"

const containerStyle = {
    width: '100%',
    height: '400px'
};


class Manager extends Component {
    constructor(props) { 
        super(props);        
    }
    
    toggleStep = this.props.toggleStep
    validateOnChange = this.props.validateOnChange
    hasError = this.props.hasError
    handleSubmit = this.props.handleSubmit
    formRef = this.props.formRef

    render() {     
        const nom = this.props.manager.formManager.nom
        const email = this.props.manager.formManager.email
        const role = this.props.manager.formManager.role
        const description = this.props.manager.selectedRole.description
        // tab item active
        const tab1 = this.props.manager.progressValue[0]
        const tab2 = this.props.manager.progressValue[1]
        const tab3 = this.props.manager.progressValue[2]
        
        return (            
            <ContentWrapper>
                {/* START form */}
                <div className="block-center col-md-8">
                    <Form
                    innerRef={this.formRef}
                    className="form-horizontal" 
                    name="formManager" 
                    onSubmit={this.handleSubmit}
                    >
                        <Card className="card-default">
                            <CardHeader className="pb-0">
                                {/*<div className="col pr-0"> <Progress color="success-light" value={100}/> </div>*/}
                            </CardHeader>                            
                            <TabContent activeTab={this.props.manager.activeStep}>
                                {/*informations */}
                                <TabPane id="tabPane1" tabId="1">
                                    <div className="pt-3 mb-3">
                                        {/*nom */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nom</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="nom"
                                                name="nom" 
                                                placeholder="Nom" 
                                                invalid={this.hasError('formManager','nom','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={nom}
                                                />
                                                <span className="invalid-feedback">Veuillez entrer le nom du rôle</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*email */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label" htmlFor="signupInputEmail1">Adresse email</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="email"
                                                id="email"
                                                name="email" 
                                                invalid={this.hasError('formManager','email','required')||this.hasError('formManager','email','email')}
                                                onChange={this.validateOnChange}
                                                value={email}
                                                data-validate='["required", "email"]' 
                                                />
                                                { this.hasError('formManager','email','required') && <span className="invalid-feedback">Veuillez remplir ce champ</span> }
                                                { this.hasError('fformManager','email','email') && <span className="invalid-feedback">Cette adresse mail n'est pas valide</span> }                                                                                         
                                            </div>
                                        </FormGroup>
                                        {/*role */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Rôle</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="select"
                                                id="role"
                                                name="role" 
                                                placeholder="Affecté un rôle à ce manager" 
                                                invalid={this.hasError('formManager','role', 'required')}
                                                onChange={this.validateOnChange}
                                                value={role}
                                                data-validate='["required"]' 
                                                >
                                                    {this.props.manager.roles.map((role) =>
                                                        <option> {role.nom} </option>
                                                    )}
                                                <span className="invalid-feedback">Veuillez sélectionner un rôle pour ce manager</span>
                                                </Input>
                                                <small> {description} </small>                                                                                            
                                            </div>
                                        </FormGroup>
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        {/*<Button color="secondary">Previous</Button>*/}
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        type="submit" 
                                        //onClick={this.toggleStep('2')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>   
                                </TabPane>
                            </TabContent>                                            
                        </Card>
                        {/* END card */}
                    </Form>
                </div>
            </ContentWrapper>
            );
    }
 
}

export default Manager;
